.App {
  /*   font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif; */
  background: url(/World-Clocks/static/media/earth.08381d4b.png);
  font-family: "Poppins", sans-serif;
  background-size: cover;
  margin: 0;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
}

.boards {
  display: flex;
  color: white;
  width: 95%;
  margin: auto;
}

h1 {
  text-align: center;
  margin: 0;
  color: white;
  padding: 30px;
}

h2 {
  margin-top: 0;
  text-align: center;
}

.board {
  height: 75vh;
  border-radius: 25px;
  background: rgba(20, 13, 59, 0.8);
  align-items: center;
  margin: 0 10px 10px 10px;
  padding: 30px;
}

.clock-board {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, 1fr);
  width: 70%;
  overflow-y: hidden;
  align-content: space-around;
}

.find-city-board {
  width: 25%;
}

.clock-space {
  position: relative;
}

.clock-space:hover .clock {
  opacity: 0.5;
}

.clock-space:hover .clock:after {
  visibility: visible;
}

.clock-space .clock {
  width: 5rem;
  height: 5rem;
  border: 15px solid #69299c;
  border-radius: 50%;
  margin: 20px auto;
  position: relative;
  padding: 2rem;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #efefef, inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
  background: url(/World-Clocks/static/media/clockface.63f2287e.png);
  background-size: cover;
  background-color: white;
}

.clock-space .clock:after {
  position: absolute;
  top: -20%;
  right: -20%;
  color: white;
  font-size: 2rem;
  visibility: hidden;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\F00D";
  cursor: pointer;
}

.clock-space .clock .clock-face {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  /* account for the height of the clock hands */
}

.clock-space p {
  color: #fff;
  text-align: center;
  font-size: 1.5rem;
  margin-top: 0;
}

.hand {
  background: black;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform-origin: 0%;
          transform-origin: 0%;
  /*pivot point; default 50%*/
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  transition: all 0.05s;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
}

form {
  width: 90%;
  display: flex;
  justify-content: space-around;
  margin: 20px;
}

form input {
  width: 100%;
  padding: 7px;
}

.cityCard {
  border-bottom: 1px solid grey;
  padding: 10px;
  cursor: pointer;
}

.cityCard:hover {
  background-color: #69299c;
}

